body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body{
  font: 1.5rem/1.2 'Open-Sans', san-serif;
  background: #817164;
  position: relative;
  color: #C9D0A4;
}


*{
  box-sizing: border-box;
}

.wrap{
  width: 100%;
  height: 100%;
}

.container{
  max-width: 1170px;
  margin: auto;
  /* position: relative; */
  height: calc(100% - 111px);
  padding: 10px;
}

.btn{
  border: none;
  padding: 10px 20px;
  border-radius: 3px;
  font-size: 18px;
  cursor: pointer;
  font-weight: bold;
  transition: .8;
}

.btn:hover{
  transform: scale(1.2);
}

.loader-wrap{
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(37, 37, 37, 0.795);
  width: 100%;
  height: 100%;
}